export const pageQuery = graphql`
  query TagPage($tag: String) {
    allJsonJson(
      filter: { tags: { in: [$tag] } }
    ) {
      totalCount
      edges {
        node {
          value
          path
          tags
        }
      }
    }
  }
`;


